.container {
  position: fixed;
  top: 0;
  transition: top 0.3s;
  width: 99vw;
  min-height: fit-content;
  max-height: max-content;
  display: flex;
  flex-direction: row;
  background-color: var(--dark-green);
}

a:visited {
  text-decoration: none;
  color: var(--cream);
}

#logo {
  padding: 2em;
  width: 5em;
  height: auto;
  text-decoration: none;
}

#end {
  padding: 2em;
  width: max-content;
  align-self: center;
  margin-left: auto;
}

.icon {
  width: 3em;
  height: auto;
}
