@import "../../index.css";

.menuIcon {
  color: black;
  font-size: xx-large;
}

.menu {
  padding: 0.5em;
  position: fixed;
  z-index: 2;
  left: 3em;
  bottom: 3em;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: max-content;
  height: max-content;
  background-color: var(--cream);
  align-items: center;
  border-radius: 10em;
  gap: 0.9em;
}

.menu a {
  padding: 1em;
  border-radius: 50%;
}

.active {
  background-color: var(--shadow-green);
}

.menu a:hover {
  background-color: var(--shadow-green);
}
