.cardContainer {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  width: 100%;
  padding: 1em;
  margin-bottom: 1em;
}

.card {
  width: 45%;
  min-width: min-content;
  margin: 1em;
  height: max-content;
  text-align: center;
  align-content: center;
  background-color: var(--shadow-green);
  color: black;
  padding: 1em;
}

@media (max-width: 420px) {
  .cardContainer {
    flex-wrap: wrap;
  }
  .card {
    width: 50%;
  }
}

.skillList {
  list-style: "+ ";
}

.listEntry {
  text-align: left;
}

.level {
  color: var(--text-light);
}
